import * as ActionTypes from './ActionTypes';

export const changingMoney = money => ({
    type: ActionTypes.MONEY_CHANGE,
    payload: money,
});

export const changingDay = day => ({
    type: ActionTypes.DAY_CHANGE,
    payload: day,
});


export const SUCCESS_RESPONSE = result => ({
    type: "SUCCESS_RESPONSE",
    payload: result
});

export const FAILED_RESPONSE = result => ({
    type: "FAILED_RESPONSE",
    payload: result
});

export const successMessage = message => ({
    type: 'SUCCESS_MESSAGE',
    payload: message,
});

export const errorMessage = message => ({
    type: 'ERROR_MESSAGE',
    payload: message,
});
export const emptyMessage = () => ({
    type: 'EMPTY_MESSAGE',
});

export const addRegistration = registrations => ({
    type: 'ADD_REGISTRATION',
    payload: registrations,
});

export const stepRegistration = step => ({
    type: 'STEP_REGISTRATION',
    payload: step,
});

export const isLoading = loader => ({
    type: 'IS_LOADING',
    payload: loader,
});


export const postRegistration = (registration) => (dispatch) => {
    dispatch(emptyMessage());
    dispatch(isLoading(true));
    registration.phone = String(registration.phone).replace(/[^A-Z0-9]/g, '');
    return fetch(`${process.env.REACT_APP_API_ENDPOINT}/registration_step_one`,{
        method: 'POST',
        body: JSON.stringify(registration),
        headers: {
            'Access-Control-Allow-Origin':'*',
            'Content-Type': 'application/json',
            'Accept': 'application/json'
        },
        credentials: 'same-origin'
    })
        .then(response =>{
            if(response.ok) {
                return response;
            }
            throw response;
        })
        .then(response => response.json()).then(e=>dispatch(successMessage(e.code)))
        .then(response => dispatch(addRegistration(registration)))
        .then(response => dispatch(isLoading(false)))
        .then(response => dispatch(stepRegistration(1)))
        .then(response=> {localStorage.setItem("step", 1)})
        // .catch(r=>r.json()).then(e=> dispatch(errorMessage(e.errors.iin ? "ИНН уже зарегистрирован. Вы будете перенаправлены на страницу авторизация! ": "" || e.errors.password ? "Пароли не совпадают": "" ||  e.errors))).then(()=>dispatch(isLoading(false)))
     
}

function checkIf(val) {
    if(val) {
        return val
    }
}
function checkIIN(val) {
    if(val && val.iin) {
        document.location.replace("https://www.i-credit.kz/login")
    }else {
        console.log("not working")
    }
}

export const postRegistrationCode = (registration) => (dispatch) => {
    dispatch(emptyMessage());
    dispatch(isLoading(true));
    console.log(registration)
    return fetch(`${process.env.REACT_APP_API_ENDPOINT}/checkPartOne`,{
        method: 'POST',
        body: JSON.stringify(registration),
        headers: {
            'Access-Control-Allow-Origin':'*',
            'Content-Type': 'application/json',
            'Accept': 'application/json'
        },
        credentials: 'same-origin'
    })
        .then(response =>{
            if(response.ok) {
                return response;
            }
            const error = new Error(`Error ${response.status}: ${response.statusText}`);
            error.response = response;
            throw error;
        },
        error => {
            const errmess = new Error(error.message);
            throw errmess;
        })
        .then(response => response.json().then(data => {
            if(data.success === true) {
                localStorage.setItem("token", data.id);
                dispatch(stepRegistration(2))
                localStorage.setItem("step",2);
                dispatch(isLoading(false));
            }
            if(data.error) {
                dispatch(errorMessage(data.error));
                dispatch(isLoading(false));
            }
        }))
        .catch(error => dispatch(errorMessage(error.message ? "Почта или номер телефона существует" : ""))).then(()=>dispatch(isLoading(false)));
}

function checkError(val) {
    
    if(val.success === true) {
        return true;
    }
    if(val.error) {
        return false;
    }
    return false;
}

export const postRegistrationSecond = (registration) => (dispatch) => {
    var object = {};
    object.id = localStorage.getItem('token');
    const finalObjects = {...object, ...registration}
    dispatch(isLoading(true));
    dispatch(emptyMessage());
    return fetch(`${process.env.REACT_APP_API_ENDPOINT}/registration_step_two`,{
        method: 'POST',
        body: JSON.stringify(finalObjects),
        headers: {
            'Access-Control-Allow-Origin':'*',
            'Content-Type': 'application/json',
            'Accept': 'application/json',
            Authorization: `Bearer ${localStorage.getItem('token')}`,

        },
        credentials: 'same-origin'
    })
        .then(response =>{
            if(response.ok) {
                return response;
            }
            throw response;
        })
        .then(response => response.json())
        .then(response => dispatch(isLoading(false)))
        .then(response => dispatch(stepRegistration(3))).then(() => dispatch(emptyMessage()))
        .then(response=> {localStorage.setItem("step", 3)})
        .catch(r => r.json().then(e => dispatch(errorMessage("Что то пошло не так" || e.errors.relative_name || e.errors.relative_last_name || e.errors.relative_phone_number || e.errors.apartment ||e.message)))).then(() => dispatch(isLoading(false)));

}

export const dateLoading = () => ({
    type: "DATE_LOADING"
})
export const dateFailed = errmess => ({
    type: 'DATE_FAILED',
    payload: errmess,
});

export const regionsSucces = region => ({
    type: 'REGION_SUCCESS',
    payload: region,
});


export const addLink = link => ({
    type: "ADD_LINK",
    payload:link
})
export const postRegistrationThird = (registration) => (dispatch) => {
    dispatch(isLoading(true));
    var object = {};
    object.id = localStorage.getItem('token');
    const finalObjects = {...object, ...registration}
    return fetch(`${process.env.REACT_APP_API_ENDPOINT}/last_step`,{
        method: 'POST',
        body: JSON.stringify(finalObjects),
        headers: {
            'Access-Control-Allow-Origin':'*',
            'Content-Type': 'application/json',
            'Accept': 'application/json',
            Authorization: `Bearer ${localStorage.getItem('token')}`,

        },
        credentials: 'same-origin'
    })
        .then(response =>{
            if(response.ok) {
                return response;
            }
            throw response;
        })
        .then(response => response.json())
        .then(response => dispatch(successMessage('Успешно')))
        .then(response=>(localStorage.clear()))
        .then(response => setTimeout(() => {dispatch(stepRegistration(0))},6000) )
        .then(response=> dispatch(isLoading(false)))
        .catch(r => r.json().then(e =>  dispatch(errorMessage( "Что то пошло не так" ||e.errors.id_card_number ? "Номер удостворение личности уже зарегистрирован" : "" || e.errors.iban_account || e.errors.card_number || e.errors ||  null)))).then(() => dispatch(isLoading(false)))
}

export const fetchRegions = (region_id) => dispatch => {
    dispatch(dateLoading(true));
    return fetch(`https://api.money-men.kz/api/city/${region_id}`, {
      headers: {
        'Content-Type': 'application/json',
        Accept: 'application/json',
      },
    })
      .then(response => {
        if (response.ok) {
          return response;
        }
  
        const error = new Error(`Error ${response.status}: ${response.statusText}`);
        error.response = response;
        throw error;
      },
      error => {
        const errmess = new Error(error.message);
        throw errmess;
      })
      .then(response => response.json())
      .then(regions => dispatch(regionsSucces(regions)))
      .catch(error => dispatch(dateFailed(error.message)));
  };

export const oplata = (oplata) => (dispatch) => {
    dispatch(isLoading(true));
    return fetch(`${process.env.REACT_APP_API_ENDPOINT}/make_payment123`,{
        method: 'POST',
        body: JSON.stringify(oplata),
        headers: {
            'Access-Control-Allow-Origin':'*',
            'Content-Type': 'application/json',
            'Accept': 'application/json'
        },
        credentials: 'same-origin'
    })
    .then(response => {
        if (response.ok) {
          return response;
        }
  
        const error = new Error(`Error ${response.status}: ${response.statusText}`);
        error.response = response;
        throw error;
      },
      error => {
        const errmess = new Error(error.message);
        throw errmess;
      })
      .then(response => response.json())
      .then(response=>document.location.replace(response[0] + "?" + response[1]))
      .then(response=> dispatch(isLoading(false)))
      .catch(error => dispatch(dateFailed(error.message))).then(() => dispatch(isLoading(false)));
}


export const addFeedback = (feedback) => (dispatch) => {
    dispatch(isLoading(true));
    return fetch(`${process.env.REACT_APP_API_ENDPOINT}/send_complaint`,{
        method: 'POST',
        body: JSON.stringify(feedback),
        headers: {
            'Access-Control-Allow-Origin':'*',
            'Content-Type': 'application/json',
            'Accept': 'application/json'
        },
        credentials: 'same-origin'
    })
    
    .then(response =>{
        if(response.ok) {
            return response;
        }
        throw response;
    })
    .then(response=> dispatch(successMessage("Успешно отправлено! Спасибо за отзыв"))).then(() =>  setTimeout(() => {
        dispatch(emptyMessage())
    }, 8000))
    .then(response=> dispatch(isLoading(false)))
    .catch(r => r.json().then(e =>  dispatch(errorMessage(e.errors.iin || e.errors.comment || e.errors.email || e.errors ||  null)))).then(() => dispatch(isLoading(false)))
}
